export default () => {
    if (process.env.STORYBOOK) {
        return '/mockData';
    }

    switch (process.env.ENV) {
        case 'prod':
        case 'rel':
            return '/heroku';
        // if  proxy  IT API
        // else  proxy u 
        //        <a>  u  false
        default:
            return '/local';
    }
};
